import React from "react";
import Image from "next/image";
import Link from "next/link";
import { usePathname } from "next/navigation";

import { Tabs, TabsList, TabsTrigger } from "../ui/tabs";

import { cn } from "@/lib/utils";
import {
  NavigationMenu,
  NavigationMenuContent,
  NavigationMenuItem,
  NavigationMenuLink,
  NavigationMenuList,
  NavigationMenuTrigger,
  navigationMenuTriggerStyle,
} from "@/components/ui/navigation-menu";
import { buttonVariants } from "../ui/button";

const navData: { title: string; href: string }[] = [
  {
    title: "Home",
    href: "/",
  },
  {
    title: "Courses",
    href: "/courses",
  },
  {
    title: "About Us",
    href: "/about",
  },
  {
    title: "Pricing",
    href: "pricing",
  },
  {
    title: "Contact",
    href: "/contact",
  },
];

function NavBar() {
  const path = usePathname();

  return (
    <nav className=" h-[99px] flex items-center justify-between px-[132px] pt-5 pb-6">
      <div className=" h-[55px] flex items-center gap-[50px]">
        <div className=" w-[54px] h-[54px] rounded-lg bg-primary flex items-center justify-center">
          <Link href={"/"}>
            <Image
              src={"/assets/shape-17.png"}
              alt="logo"
              width={32.5}
              height={32.5}
              className="object-fill"
            />
          </Link>
        </div>
        <div className="flex gap-[26px]">
          <NavigationMenu>
            <NavigationMenuList>
              <NavigationMenuItem>
                {navData.map((item) => (
                  <Link
                    key={item.href}
                    href={item.href}
                    legacyBehavior
                    passHref
                  >
                    <NavigationMenuLink
                      className={cn(
                        navigationMenuTriggerStyle(),
                        "h-14 w-auto px-6 text-lg"
                      )}
                      active={path.includes(item.href)}
                    >
                      {item.title}
                    </NavigationMenuLink>
                  </Link>
                ))}
              </NavigationMenuItem>
            </NavigationMenuList>
          </NavigationMenu>
        </div>
      </div>
      <Tabs
        defaultValue="login"
        className=" flex items-center justify-center h-[55px] w-[214px]"
      >
        <TabsList className=" h-full text-lg">
          <TabsTrigger value="sign up" className=" text-lg h-full">
            {" "}
            <Link href="/signup">Sign Up</Link>{" "}
          </TabsTrigger>
          <TabsTrigger value="login" className=" text-lg h-full">
            <Link href="/login">Login</Link>{" "}
          </TabsTrigger>
        </TabsList>
      </Tabs>
    </nav>
  );
}

export default NavBar;
